<template>
    <div>
        <div class="panel admin-panel">
            <div class="panel-head"><strong class="icon-reorder"> 班级管理</strong></div>
            <div class="padding border-bottom">
                <a class="button border-yellow" href="#/manage/classesAdd"><span class="icon-plus-square-o"></span> 添加内容</a>
            </div>
            <el-table
                    height="250"
                    :data="tableData"
                    stripe
                    style="width: 100%">
                <el-table-column prop="cid" label="cid"></el-table-column>
                <el-table-column prop="cname" label="班级名称"></el-table-column>
                <el-table-column prop="sname" label="所属阶段"></el-table-column>
                <el-table-column prop="dname" label="所属方向"></el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <router-link :to="{name:'classesUpdate',query:{cid:scope.row.cid}}" class="el-button el-button--default el-button--mini"> 编辑</router-link>
                        <el-button
                                size="mini"
                                type="danger"
                                @click="handleDelete(scope.row.cid)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
    </div>
</template>
<script>
    export default {
        name:'manageClasses',
        data(){
            return{
                tableData: []
            }
        },
        methods:{
            handleDelete(cid){
                this.$http.post('/home/classes/classesDelete.php',{cid}).then(res=>{
                    if(res.body=='ok'){
                        this.tableData = this.tableData.filter(ele=>ele.cid != cid)
                    }else if(res.body=='error'){
                        this.$message({
                            showClose: true,
                            message: '操作失败',
                            type: 'warning'
                        });
                    }
                })
            }
        },
        mounted(){
            this.$http.post('/home/classes/manageClasses.php').then(res=>{
                this.tableData=res.body;
            })
        },
        beforeCreate(){
            if(!localStorage.admin){
                this.$message({
                    showClose: true,
                    message: '请先登录',
                    type: 'warning'
                });
                this.$router.push({name:'login'})
            }
        }
    }
</script>
<style>

</style>


